$nippleColor: #ffbfbf;
$nippleBaseColor: #ffbfbf;
$backNippleColor: #ffccc0;

.nippleTop {
  border-radius: 100%;
  width: 30px;
  height: 5px;
  transition: background 500ms ease-in-out;
}

.nipple {
  background: $nippleColor;
  border-radius: 100%;
  width: 70px;
  height: 70px;
  transition: background 500ms ease-in-out;
  border-top: 3px solid #f7a471;
}
.container[fast="true"],
.container[stretched="true"] {
  .nipple {
    background: rgb(255, 161, 172);
  }
}

.nippleBody {
  border-radius: 10px;
  transform: translate(0, calc(-50% + 35px));
  background: linear-gradient(180deg, rgb(255, 176, 176) 10%, $nippleBaseColor 100%);
  width: 70px;
  height: 0px;
  padding-top: 35px;
  box-sizing: content-box;
  border-radius: 0 0 50px 50px;
}

.nippleBase {
  border-bottom: 6px solid rgba(0, 0, 0, 0.01);
  background: $nippleBaseColor;
  height: 200px;
  width: 200px;
  border-radius: 100%;
}

.backNipple {
  border-bottom: 11px solid rgba(0, 0, 0, 0.02);
  border-radius: 100%;
  height: 500px;
  width: 400px;
  background: $backNippleColor;
}
